import Vue from 'vue'
import {GET_PLAYER_DETAILS_SUCCESS, GET_PLAYER_DETAILS_FAILD, REQUEST_PLAYER_DETAILS, UPDATE_USER_COIN} from '../consts'
import {GET_PLAYER_DETAILS_HISTORY_SUCCESS, GET_PLAYER_DETAILS_HISTORY_FAIL, REQUEST_PLAYER_DETAILS_HISTORY} from '../consts'

export default {
  state: {
    isFetching: false,
    historyIsFetcing:false,
    CostHistory:[],
    DeTailHistory:[],
    PlayerHistory:[],
    playerInfo:{},
    Historys_total:1,
    Historys_page:1,
    CostHistorys_page:1,
    DetailHostys_page:1,
    PlayerHistorys_page:1,
    CostHistorys_total : 1,
    DetailHostys_total : 1,
    PlayerHistorys_total : 1,
    tableShowName: "cost",
    pageShow:1000
  },
  mutations: {
    [GET_PLAYER_DETAILS_SUCCESS] (state,playerInfo) {
      state.isFetching = false
      state.playerInfo = playerInfo
      
    },
    [GET_PLAYER_DETAILS_FAILD] (state) {
      state.isFetching = false
      state.playerInfo = null
    },
    [REQUEST_PLAYER_DETAILS] (state) {
      state.isFetching = true
      state.playerInfo = null
    },
    [REQUEST_PLAYER_DETAILS_HISTORY] (state) {
      state.historyIsFetcing = true
    },
    [GET_PLAYER_DETAILS_HISTORY_FAIL] (state) {
      state.historyIsFetcing = false
      state.CostHistory = []
      state.DeTailHistory = []
      state.PlayerHistory = []
      state.CostHistorys_total = 1
      state.DetailHostys_total = 1
      state.PlayerHistorys_total = 1
      state.CostHistorys_page = 1
      state.DetailHostys_page = 1
      state.PlayerHistorys_page = 1
    },
    [GET_PLAYER_DETAILS_HISTORY_SUCCESS] (state,history) {
      state.historyIsFetcing = false
      state.CostHistory = history.CostHistory.CostHistorys
      state.DeTailHistory = history.DeTailHistory.DetailHostys
      state.PlayerHistory = history.PlayerHistory.PlayerHistorys
      state.CostHistorys_total = history.CostHistory.CostHistorys_total
      state.DetailHostys_total = history.DeTailHistory.DetailHostys_total
      state.PlayerHistorys_total = history.PlayerHistory.PlayerHistorys_total
    }
  },
  actions: {
    async getPlayerDetails (store,id) {
      if (store.state.isFetching) {
        return
      }
      store.commit(REQUEST_PLAYER_DETAILS)
      try {
        let res = await Vue.api.get('/user/findUser',{
          params: {
            id:id,
          }
        })
        if(!res.data.users){
          throw new Error('Error fetch users')
        }
        store.commit(GET_PLAYER_DETAILS_SUCCESS,res.data.users)
      } catch (e) {
        store.commit(GET_PLAYER_DETAILS_FAILD)
        throw e
      }
    },
    async setPlayerDetails (store,info) {
      if (store.state.isFetching) {
        return
      }
      if(!info.playerInfo && !info.id) {
        return 
      }
      if(!info.playerInfo && info.id) {
         await store.dispatch('getPlayerDetails',info.id)
      }else{
        store.commit(GET_PLAYER_DETAILS_SUCCESS,info.playerInfo)
      }
      await store.dispatch('getPlayerDetailsHistory',info.id)
    },
    async AddCard (store,number) {
        //add car number
      if (store.state.isFetching) {
        return
      }
      if (store.state.historyIsFetcing) {
        return
      }
      if(!store.state.playerInfo ||!store.state.playerInfo.id){
        return
      }
      let ret = await Vue.api.get('/user/addCound', {
          params: {
            id:store.state.playerInfo.id,
            number:number
          }
      })
      ret.data.id = store.state.playerInfo.id
      return ret.data
    },
    async getPlayerDetailsHistory (store) {
      if (store.state.isFetching) {
        return
      }
      if (store.state.historyIsFetcing) {
        return
      }

    if(!store.state.playerInfo.id){
        return
      }
      store.commit(REQUEST_PLAYER_DETAILS_HISTORY)
      
      try{
        let ret = await Vue.api.get('/user/history', {
           params: {
            id:store.state.playerInfo.id,
            page:{
              CostHistorys:store.state.CostHistorys_page,
              DetailHostys:store.state.DetailHostys_page,
              PlayerHistorys:store.state.PlayerHistorys_page
           }
          }
         })
        store.commit(GET_PLAYER_DETAILS_HISTORY_SUCCESS,ret.data.history)
      }
      catch(e){
        console.error(e)
        store.commit(GET_PLAYER_DETAILS_HISTORY_FAIL)
      }
    }
  }
}



