<template>
	<section>
		<!--工具条-->
		<el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
			<el-form :inline="true">
				<el-form-item label="玩家id">
					<el-input v-model="playerInfo.id"></el-input>
				</el-form-item>	
				<el-form-item label="玩家姓名">
					<el-input v-model="playerInfo.name"></el-input>
				</el-form-item>
				<el-form-item label="账户id">
					<el-input v-model="playerInfo.account"></el-input>
				</el-form-item>
				<el-form-item label="房卡">
					<el-input v-model="playerInfo.gems"></el-input>
				</el-form-item>
				<el-form-item label="所在房间">
					<el-input v-model="playerInfo.roomid"></el-input>
				</el-form-item>
				<!-- <el-form-item label="活动区域">
					<el-select placeholder="请选择活动区域">
						<el-option label="区域一" value="shanghai"></el-option>
						<el-option label="区域二" value="beijing"></el-option>
					</el-select>
				</el-form-item> -->
				<!-- <el-form-item label="活动时间">
					<el-col :span="11">
						<el-date-picker type="date" placeholder="选择日期"  style="width: 100%;"></el-date-picker>
					</el-col>
					<el-col class="line" :span="2">-</el-col>
					<el-col :span="11">
						<el-time-picker type="fixed-time" placeholder="选择时间"  style="width: 100%;"></el-time-picker>
					</el-col>
				</el-form-item> -->
			</el-form>
			<br/>
			<br/>
		</el-col>
		<el-form :inline="true" class="toolbar" style="padding-bottom: 10px;">
			<el-form-item>
				<el-button type="primary" @click='AddCardNumber' :loading="isFetching">增加房卡</el-button>
			</el-form-item>
			<el-form-item>
				 <el-input-number v-model="addNumber" placeholder="增加房卡"></el-input-number>
			</el-form-item>
			<el-form-item>
				<el-button type="primary">退出房间</el-button>
			</el-form-item>	
			<el-form-item>
				<el-button type="primary">刷新</el-button>
			</el-form-item>
		</el-form>
		<!--列表-->
		<template>
			<el-tabs v-model="tableShowName">
    		<el-tab-pane label="玩家消费记录" name="cost">
    			<el-table :data="CostHistory" highlight-current-row v-loading="historyIsFetcing" style="width: 100%;">
				<el-table-column type="id" width="60">
				</el-table-column>
				<el-table-column prop="type" label="类型" width="120" sortable>
				</el-table-column>
				<el-table-column prop="roomid" label="房间" width="100"  sortable>
				</el-table-column>
				<el-table-column prop="costTime" label="时间" sortable>
				</el-table-column>
				<el-table-column prop="gameid" label="游戏id" sortable>
				</el-table-column>
				<el-table-column prop="cost" label="消费量" sortable>
				</el-table-column>	
				<el-table-column  label="房间详情">
					<template scope="scope">
      					<el-button type="info" size="small">查看</el-button>
					<!-- <el-button size="small" @click="selectRow(scope.$index, scope.row)">查看</el-button> -->
						<!-- <el-tooltip class="item" effect="dark" height='20' :content="checkConf(scope.$index, scope.row)" placement="bottom-start">
    				</el-tooltip> -->
					</template>
				</el-table-column>
					</el-table>
					<el-col :span="24" class="toolbar">
						<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="pageShow" :total="CostHistorys_total" :current-page='CostHistorys_page' style="float:right;">
						</el-pagination>
					</el-col>
    		</el-tab-pane>	
    	<el-tab-pane label="玩家行为记录" name="entry">
    		<el-table :data="DeTailHistory" highlight-current-row v-loading="historyIsFetcing" style="width: 100%;">
					<el-table-column type="index" width="60">
					</el-table-column>
					<el-table-column prop="type" label="类型"  sortable>
					</el-table-column>
					<el-table-column prop="roomid" label="房间" sortable>
					</el-table-column>
					<el-table-column prop="time" label="时间"  sortable>
					</el-table-column>
					<el-table-column prop="creator" label="创建人"  sortable>
					</el-table-column>
					<el-table-column prop="isDestory" label="房间存在" sortable>
					</el-table-column>
					<el-table-column prop="destoryTime" label="销毁时间"  sortable>
					</el-table-column>	
				<!-- 	<el-table-column prop="conf" :formatter="checkConfDesc"  label="说明" >
					</el-table-column>	 -->
				</el-table>
				<el-col :span="24" class="toolbar">
					<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="pageShow" :total="PlayerHistorys_total" :current-page='PlayerHistorys_page' style="float:right;">
					</el-pagination>
				</el-col>
    	</el-tab-pane>
    	<el-tab-pane label="玩家游戏记录" name="play">
    		<el-table :data="PlayerHistory" highlight-current-row v-loading="historyIsFetcing" style="width: 100%;">
					<el-table-column type="index" width="60">
					</el-table-column>
					<el-table-column prop="type" label="局数" width="120" sortable>
					</el-table-column>
					<el-table-column prop="gameid" label="游戏id" width="100" sortable>
					</el-table-column>
					<el-table-column prop="roomid" label="房间" width="100" sortable>
					</el-table-column>
					<el-table-column prop="time" label="时间"  sortable>
					</el-table-column>
					<el-table-column prop="seats" :formatter="checkConfSeat"  label="座位信息" min-width="180"  sortable>
					</el-table-column>
				</el-table>
				<el-col :span="24" class="toolbar">
					<el-pagination layout="prev, pager, next" @current-change="handleCurrentChange" :page-size="pageShow" :total="PlayerHistorys_total" :current-page='PlayerHistorys_page' style="float:right;">
					</el-pagination>
				</el-col>
    	</el-tab-pane>
			</el-tabs>		
		</template>
	</section>
</template>
<script>
	import { getUserList } from '../../api/api';
	import { Form } from './Form.vue'
	import { mapState, mapActions } from 'vuex'
	//import NProgress from 'nprogress'
	export default {
		data() {
			return {
				users: [
				],
				addNumber:undefined,
				tableShowName:"cost",
				//isFetching:true
			}
		},
		computed: {
   		 ...mapState({
    			isFetching :state=> state.user.playerList.playerDetail.isFetching,
    			historyIsFetcing :state=> state.user.playerList.playerDetail.historyIsFetcing,
    			CostHistory :state=> state.user.playerList.playerDetail.CostHistory,
    			PlayerHistory :state=> state.user.playerList.playerDetail.PlayerHistory,
    			DeTailHistory :state=> state.user.playerList.playerDetail.DeTailHistory,
    			CostHistorys_total :state=> state.user.playerList.playerDetail.CostHistorys_total,
    			DetailHostys_total :state=> state.user.playerList.playerDetail.DetailHostys_total,
    			PlayerHistorys_total :state=> state.user.playerList.playerDetail.PlayerHistorys_total,
    			CostHistorys_page :state=> state.user.playerList.playerDetail.CostHistorys_page,
    			DetailHostys_page :state=> state.user.playerList.playerDetail.DetailHostys_page,
    			PlayerHistorys_page :state=> state.user.playerList.playerDetail.PlayerHistorys_page,
    			playerInfo :state=> state.user.playerList.playerDetail.playerInfo,
    			DeTailHistory :state=> state.user.playerList.playerDetail.DeTailHistory,
    			pageShow :state=> state.user.playerList.playerDetail.pageShow
    		})
  		},
		methods: {
			//性别显示转换
			...mapActions([
					'getPlayerDetails',
					'getPlayerDetailsHistory',
					'AddCard'
				]),
			formatSex: function (row, column) {
				return row.sex == 1 ? '男' : row.sex == 0 ? '女' : '未知';
			},
			handleCurrentChange () {

			},
			checkConf (index,row) {
				return JSON.stringify(row.description)
			},
			checkConfSeat(obj){
				let name = `
					${obj.seats[0].name}:${obj.seats[0].score}		
					${obj.seats[1].name}:${obj.seats[1].score}
					${obj.seats[2].name}:${obj.seats[2].score} 
					${obj.seats[3].name}:${obj.seats[3].score}
				`
				return name
			},
			checkConfDesc(obj){
				return JSON.stringify(obj)
			},
			async AddCardNumber() {
				if(this.addNumber === 0) {
					return 
				}
				if(this.addNumber < 0) {
					return 
				}
				let ret = await this.AddCard(this.addNumber)
				if(!ret) return 
				if(ret.rows > 0){
					await this.getPlayerDetails(ret.id)
				}
			}

		},
		mounted() {
			if(!this.playerInfo||!this.playerInfo.id){
				return this.$router.replace('/table')
			}
			return 
			this.getPlayerDetailsHistory().then(_=>{
				console.log(this.CostHistorys_total)
			})
		}
	};

</script>

<style scoped>

</style>


